@import url("https://fonts.googleapis.com/css?family=Merriweather|Fira+Sans&display=swap");

:root {
  --red: 200;
}

.App {
  margin: 0 auto;
  color: white;
}

.location {
  text-align: center;
}

.card {
  margin: 0 auto;
  background: linear-gradient(to bottom, rgba(var(--red), 200, 200), lightblue);
  width: 200px;
  height: 240px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  border-radius: 15px;
}

.city {
  font-family: "Merriweather", sans-serif;
  font-size: 1.6rem;
}

.country {
  font-family: "Fira Sans", sans-serif;
  font-size: 1.1rem;
}

.temp {
  font-family: "Fira Sans", sans-serif;
  font-size: 2rem;
  font-weight: 200;
}

.condition {
  font-family: "Merriweather", sans-serif;
  font-size: 1.2rem;
}
